<template>
  <div>
    <el-form :model="ruleForm1" :rules="rules1" ref="ruleForm1" label-width="100px" class="demo-ruleForm">
      <el-form-item label="" prop="region1">
        <el-select v-model="ruleForm1.region1" placeholder="">
          <el-option label="" value="rf1-1"></el-option>
          <el-option label="" value="rf1-2"></el-option>
          <el-option label="" value="rf1-3"></el-option>
          <el-option label="" value="rf1-4"></el-option>
        </el-select>
      </el-form-item>
      <div class="erji">
        <el-form-item label="" prop="name1">
          <el-input style="width:218px" v-model="ruleForm1.name1"></el-input>
        </el-form-item>
        <el-form-item label="" prop="num1">
          <el-input style="width:218px" v-model="ruleForm1.num1"></el-input>
        </el-form-item>
        <el-form-item label="" prop="pic1">
          <img src="../../../assets/user_pic.jpg" alt="">
        </el-form-item>
      </div>  
    </el-form>
    <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
      <el-form-item label="" prop="region2">
        <el-select v-model="ruleForm2.region2" placeholder="">
          <el-option label="" value="rf2-1"></el-option>
          <el-option label="" value="rf2-2"></el-option>
          <el-option label="" value="rf2-3"></el-option>
          <el-option label="" value="rf2-4"></el-option>
        </el-select>
      </el-form-item>
      <div class="erji">
        <el-form-item label="" prop="dwname2">
          <el-input style="width:500px" v-model="ruleForm2.dwname2"></el-input>
        </el-form-item>
        <el-form-item label="" prop="name2">
          <el-input style="width:218px" v-model="ruleForm2.name2"></el-input>
        </el-form-item>
        <el-form-item label="" prop="num2">
          <el-input style="width:218px" v-model="ruleForm2.num2"></el-input>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input style="width:218px" v-model="ruleForm2.address2"></el-input>
        </el-form-item>
        <el-form-item label="" prop="time2">
          <el-date-picker
            v-model="ruleForm2.time2"
            type="date"
            placeholder=""
            :picker-options="pickerOptions0">
          </el-date-picker>
          <el-checkbox v-model="checked"></el-checkbox>
        </el-form-item>
        <el-form-item label="" prop="pic2">
          <img src="../../../assets/zizhi.jpg" alt="">
        </el-form-item>
      </div> 
    </el-form>
    <el-form :model="ruleForm3" :rules="rules3" ref="ruleForm3" label-width="100px" class="demo-ruleForm">
      <el-form-item label="" prop="region3">
        <el-select v-model="ruleForm3.region3" placeholder="">
          <el-option label="" value="rf3-1"></el-option>
          <el-option label="" value="rf3-2"></el-option>
          <el-option label="" value="rf3-3"></el-option>
          <el-option label="" value="rf3-4"></el-option>
          <el-option label="" value="rf3-5"></el-option>
          <el-option label="/" value="rf3-6"></el-option>
          <el-option label="" value="rf3-7"></el-option>
          <el-option label="" value="rf3-8"></el-option>
          <el-option label="" value="rf3-9"></el-option>
          <el-option label="" value="rf3-10"></el-option>
          <el-option label="" value="rf3-11"></el-option>
          <el-option label="/" value="rf3-12"></el-option>
          <el-option label="" value="rf3-13"></el-option>
          <el-option label="" value="rf3-14"></el-option>
          <el-option label="" value="rf3-15"></el-option>
          <el-option label="" value="rf3-16"></el-option>
          <el-option label="" value="rf3-17"></el-option>
        </el-select>
      </el-form-item>
      <div class="erji">
        <el-form-item label="" prop="dwname3">
          <el-input style="width:500px" v-model="ruleForm3.dwname3"></el-input>
        </el-form-item>
        <el-form-item label="" prop="name3">
          <el-input style="width:218px" v-model="ruleForm3.name3"></el-input>
        </el-form-item>
        <el-form-item label="" prop="num3">
          <el-input style="width:218px" v-model="ruleForm3.num3"></el-input>
        </el-form-item>
        <el-form-item label="" prop="address3">
          <el-input style="width:218px" v-model="ruleForm3.address3"></el-input>
        </el-form-item>
        <el-form-item label="" prop="time3">
          <el-date-picker
            v-model="ruleForm3.time3"
            type="date"
            placeholder=""
            :picker-options="pickerOptions0">
          </el-date-picker>
          <el-checkbox v-model="checked"></el-checkbox>
        </el-form-item>
        <el-form-item label="" prop="pic3">
          <img src="../../../assets/zizhi2.jpg" alt="">
        </el-form-item>
      </div>
    </el-form>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        pickerOptions0: {
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7;
          }
        },
        checked: false,
        ruleForm1:{
          region1: '',
          name1: '',
          num1:'',
          pic1:''
        },
        ruleForm2:{
          region2: '',
          dwname2: '',
          name2: '',
          num2: '',
          address2: '',
          time2: '',
          pic2:''
        },
        ruleForm3:{
          region3: '',
          dwname3: '',
          name3: '',
          num3: '',
          address3: '',
          time3: '',
          pic3:''
        },
        rules1: {
          region1: [
            { required: true, message: '', trigger: 'change' }
          ],
          name1: [
            { required: true, message: '', trigger: 'blur' }
          ],
          num1: [
            { required: true, message: '', trigger: 'blur' }
          ],
          pic1: [
            { required: true, message: '', trigger: 'blur' }
          ]
        },
        rules2: {
          region2: [
            { required: true, message: '', trigger: 'change' }
          ],
          dwname2: [
            { required: true, message: '', trigger: 'blur' }
          ],
          name2: [
            { required: true, message: '', trigger: 'blur' }
          ],
          num2: [
            { required: true, message: '', trigger: 'blur' }
          ],
          address2: [
            { required: true, message: '', trigger: 'blur' }
          ],
          time2: [
            { type: 'date', required: true, message: '', trigger: 'change' }
          ],
          pic2: [
            { required: true, message: '', trigger: 'blur' }
          ]
        },
        rules3: {
          
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<style scoped lang="scss">
  .demo-ruleForm {
    .erji {
        width: 900px;
    margin: 0px 0 30px 30px;
    border: 1px solid #e6e6e6;
    padding: 10px;

  }
  }
  
</style>


