<template>
  <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="150px" class="demo-ruleForm">
    <el-form-item label="">
      18651908626
    </el-form-item>
    <el-form-item label="" prop="storename">
      <el-input style="width:350px;" v-model="ruleForm.storename"></el-input>
    </el-form-item>
    <el-form-item label="" prop="phone">
      <el-input style="width:220px;" v-model="ruleForm.phone"></el-input>
    </el-form-item>
    <el-form-item label="" prop="name">
      <el-input style="width:220px;" v-model="ruleForm.name"></el-input>
    </el-form-item>
    <el-form-item label="" prop="storetype">
      <el-select v-model="ruleForm.storetype" placeholder="">
        <el-option label="" value="shanghai"></el-option>
        <el-option label="" value="beijing"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="" prop="time">
      <el-radio-group v-model="ruleForm.time">
        <el-radio label="24"></el-radio>
        <el-radio label=""></el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="" prop="city">
      <el-input style="width:350px;" v-model="ruleForm.city"></el-input>
    </el-form-item>
    <el-form-item label="" prop="adress">
      <el-input style="width:350px;"  v-model="ruleForm.adress"></el-input>
      <el-button type="primary"></el-button>
      <div>
        <img src="../../../assets/store_map.jpg" alt="">
      </div>
    </el-form-item>
    <el-form-item label="" prop="storepic">
      <div>
        <div class="storepic">
          <img src="../../../assets/store_pic.jpg" alt="">
          <el-upload
            class="avatar-uploader"
            action="https://jsonplaceholder.typicode.com/posts/"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload">
            <img v-if="imageUrl" :src="imageUrl" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </div>
      </div>
    </el-form-item>
    <el-form-item label="Logo" prop="storelogo">
      <div>
        <div>
          <img src="../../../assets/store_logo.jpg" alt="">
        </div>
      </div>
    </el-form-item>
  </el-form>
</template>
<script>
  export default {
    components: {
    },
    data() {
      return {
        imageUrl: '',
        ruleForm: {
          storename: '',
          phone: '',
          name: '',
          storetype:'',
          time:'',
          city: '',
          adress: '',
          storepic: '',
          storelogo: ''
        },
        rules: {
          storename: [
            { required: true, message: '', trigger: 'blur' },
            { min: 2, max: 20, message: ' 2  20 ', trigger: 'blur' }
          ],
          phone: [
            { required: true, message: '', trigger: 'blur' },
            { min: 11, max: 11, message: '', trigger: 'blur' }
          ],
          name: [
            { required: true, message: '', trigger: 'blur' },
            { min: 2, max: 10, message: ' 2  10 ', trigger: 'blur' }
          ],
          storetype: [
            { required: true, message: '', trigger: 'change' }
          ],
          time: [
            { required: true, message: '', trigger: 'change' }
          ],
          city: [
            { required: true, message: '', trigger: 'change' }
          ],
          adress: [
            { required: true, message: '', trigger: 'change' }
          ],
          storepic: [
            { required: true, message: '', trigger: 'change' }
          ],
          storelogo: [
            { required: true, message: 'Logo', trigger: 'change' }
          ]
        }
      };
    },
    methods: {
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error(' JPG !');
        }
        if (!isLt2M) {
          this.$message.error(' 2MB!');
        }
        return isJPG && isLt2M;
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<style scoped lang="scss">
  .storepic {
  }
</style>


